@import '../../_shared';

.panel-background-primary {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100); // exact vh for mobile
  width: 100%;
  position: fixed;
  overflow: hidden;

  .panel-background-secondary {

    .panel-background-secondary-top {
      // content: '';
      // position: absolute;
      // background-color: red;
      // height: 100%;
      // width: 100%;
      // z-index: 10;
      transform: translate(-100%, -100%);
      transition: transform 0.4s;
    }

    .panel-background-secondary-bottom {
      // content: '';
      // position: absolute;
      // background-color: pink;
      // height: 100%;
      // width: 100%;
      // z-index: 10;
      transform: translate(100%, 0);
      transition: transform 0.4s;
    }

    &.animating {
      .panel-background-secondary-top {
        transform: translate(0, -100%);
      }

      .panel-background-secondary-bottom {
        transform: translate(0, 0);
      }
    }

    > div {
      z-index: 1;
      position: absolute;
    }
  }

  div {
    width: 100%;
    height: 100%;
  }

  .panel-nav {
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    // https://github.com/Schepp/CSS-Filters-Polyfill
    filter: drop-shadow( 0px 2px 3px rgba(0, 0, 0, .7));
  
    > svg {
      position: relative;
      font-size: 40px;
      display: block;
      color: #fff
    }

    &:hover {
      cursor: pointer;
    }
    
    &.hidden {
      opacity: 0;
    }
  }

  .panel-nav-up {
    top: 0;
    margin-top: 1rem;

    > svg {
      animation: bounce-up 2s infinite;
    }
  }

  .panel-nav-down {
    bottom: 0;
    margin-bottom: 1rem;

    > svg {
      animation: bounce-down 2s infinite;
    }
  }
}

@keyframes bounce-down {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
    opacity: 1;
  }
  40% {
    transform: translateY(-30px);
    opacity: 0.5;
  }
  60% {
    transform: translateY(-15px);
    opacity: 0.75;
  }
}

@keyframes bounce-up {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
    opacity: 1;
  }
  40% {
    transform: translateY(30px);
    opacity: 0.5;
  }
  60% {
    transform: translateY(15px);
    opacity: 0.75;
  }
}